:root {
  --primary-color: #0c4a6e;
  --accent-color: #047857;
  --delete-color: #b91c1c;
  --info-color: #0891b2;
  --text-color: #fff;
  --color-grey: #f1f5f9;
  --color-grey-light: #e5e5e5;
  --color-grey-dark: #64748b;
  --shadow-dark: 0 2rem 6rem rgba(black, 0.3);
  --shadow-light: 0 2rem 5rem rgba(0,0,0, 0.06);
  --line: 1px solid var(--color-grey-light);
}

body {
  background-color: var(--color-grey);
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

header,
footer {
  background: var(--primary-color);
  color: var(--text-color);
  padding: 1rem 2rem;
}

aside {
  background-color: var(--color-grey-light);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
aside > * {
  display: inline-block;
  margin-bottom: 1rem;
}

button > i, [data-prefix=fas] {
  pointer-events: none;
}

.btn {
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
  padding: 0.5rem 1rem;
  text-align: left;
}
.btn > i {
  margin-right: 0.5rem;
}
.btn:hover {
  background-color: var(--primary-color);
  color: var(--text-color);
  border-radius: 10px;
}

footer {
  text-align: center;
}

.hidden {
  display: none !important;
}

.active {
  display: block !important;
  visibility: visible !important;
  opacity: 1 !important;
}

.content {
  width: 100%;
  display: flex;
  flex: 1;
}
.content .links__container {
  padding: 2rem 2rem;
  width: 16rem;
  border-right: 1px solid white;
  transition: visibility 0s, opacity 0.5s linear;
}
.content .links__container > h2 {
  margin-top: 3rem;
  font-weight: 700;
  font-size: 1.2rem;
}
.content .links__container > .add__project {
  visibility: hidden;
  display: none;
  opacity: 0;
}
.content .links__container #projects {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
}
.content .form {
  display: flex;
  flex-direction: column;
}
.content .form label {
  margin-right: 1rem;
  margin-bottom: 0.5rem;
  font-size: 0.875rem;
}
.content .form > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.content .form input,
.content .form select,
.content .form option {
  padding: 0.5rem 0.75rem;
  outline: none;
  border: 1px solid var(--color-grey-dark);
  border-radius: 5px;
  margin-bottom: 1rem;
}
.content .form__controls {
  display: flex;
  justify-content: space-between;
}
.content .form__controls > .add, .content .form__controls > .reset {
  max-width: 80px;
  text-align: center;
  flex: 0 1 50%;
  color: #fff;
  border-radius: 5px;
}
.content .form__controls > .add {
  background: var(--accent-color);
}
.content .form__controls > .add:hover {
  background: var(--info-color);
}
.content .form__controls > .reset {
  background: var(--delete-color);
}
.content .form__controls > .reset:hover {
  background: var(--info-color);
}
.content .tasks__container {
  position: relative;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 2rem 2rem;
  gap: 2rem;
  transition: all 0.3s ease-in-out;
}
.content .tasks__container--top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.content .tasks__container--top > .active {
  background-color: var(--primary-color);
  color: var(--text-color);
  border-radius: 10px;
}
.content .tasks__container--top > .active:hover {
  background-color: var(--accent-color);
}
.content .tasks__container--top > h2 {
  font-weight: 700;
  font-size: 1.6rem;
}
.content .tasks__container--add-task {
  z-index: 100;
  position: absolute;
  top: 5rem;
  right: 2rem;
  display: none;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  background: var(--text-color);
  padding: 2rem 2rem;
  border-radius: 5px;
  border-top: 5px solid var(--info-color);
  box-shadow: 0px 15px 40px -25px var(--color-grey-dark);
}
.content .tasks__container--bottom {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}
.content .tasks__container--bottom > .task__box {
  max-width: 200px;
  background: white;
  border-radius: 5px;
  border-top: 5px solid var(--primary-color);
  box-shadow: 0px 15px 40px -25px var(--color-grey-dark);
  padding: 1.5rem 1rem 1rem 1rem;
  transition: all 0.3s ease-in-out;
}
.content .tasks__container--bottom > .task__box .tag {
  display: inline-block;
  background-color: var(--primary-color);
  color: var(--text-color);
  font-weight: 600;
  font-size: 0.75rem;
  padding: 0.5em 0.75em;
  line-height: 1;
  border-radius: 6px;
}
.content .tasks__container--bottom > .task__box .tag + * {
  margin-top: 1rem;
}
.content .tasks__container--bottom > .task__box .tag::before {
  content: "Active";
}
.content .tasks__container--bottom > .task__box + .completed > .tag {
  background-color: var(--accent-color);
}
.content .tasks__container--bottom > .task__box + .completed > .tag::before {
  content: "Completed";
}
.content .tasks__container--bottom > .task__box:has(#delete_task:hover) {
  border-top: 5px solid var(--delete-color);
}
.content .tasks__container--bottom > .task__box:has(#edit_task:hover) {
  border-top: 5px solid var(--primary-color);
}
.content .tasks__container--bottom > .task__box:hover {
  transform: translateY(-1%);
  border-top: 5px solid var(--accent-color);
}
.content .tasks__container--bottom > .task__box .task_info > p, .content .tasks__container--bottom > .task__box .task_info > h2 {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}
.content .tasks__container--bottom > .task__box .task_info > h2 {
  margin-top: 1.5rem;
  font-weight: 700;
  font-size: 1.2rem;
}
.content .tasks__container--bottom > .task__box .task__controls {
  margin-top: 2rem;
  display: flex;
}
.content .tasks__container--bottom .completed {
  opacity: 0.5;
  background: var(--color-grey-light);
  border-top: 5px solid var(--accent-color);
}
.content .tasks__container--bottom .completed:hover {
  background: white;
}

/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
}
.tooltip > .btn__control {
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
  padding: 0.5rem 1rem 0.5rem 0;
}
.tooltip > .btn__control:hover {
  color: var(--info-color);
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 80px;
  background-color: var(--color-grey-dark);
  font-size: 0.8rem;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  /* Position the tooltip text */
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
  margin-left: -50px;
  /* Fade in tooltip */
  opacity: 0;
  transition: opacity 0.3s;
}

/* Tooltip arrow */
.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
